 position: absolute;
 height: auto;
 width: 90%;
 margin: 20px;

 .wrapper-header {
  height: 5vh;
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  font-size: 1.1vw;

  .wrapper-header_select {
   background: #fff;
   border: none;
   font-size: 1em;

   .languageOption {
    display: block;
    padding-top: 5px;
    cursor: pointer;
   }
  }

  .wrapper-header_logo img {
    width: 2.5vw;
    height: auto;
  }

  .wrapper-header_left {
   width: 40vw;
   display: flex;
   align-items: center;
  }

  .wrapper-header_nav {
   display: flex;
   justify-content: space-between;
   width: 30vw;
   padding-left: 4vw;

   div {
    margin-right: 1vw;
    font-size: 1em;
   }
  }

  .wrapper-header_nav div a {
   text-decoration: none;
   color: #141414;
  }

  .wrapper-header_right {
   width: 40vw;
   display: flex;
   justify-content: flex-end;
   align-items: center;
   font-size: 1em;
  }

  .wrapper-header_contacts {
   display: flex;
   justify-content: space-between;
   width: 30vw;
   padding-right: 4vw;
   color: #FF5C00;
  }

 }

 .wrapper-main {
  height: 65vh;
  display: flex;
  justify-content: space-between;
  flex-direction: row;

  .wrapper-main_leftside {
   width: 3vw;
   height: 100%;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;

   .wrapper-main_img {
    margin-top: 3vh;
   }

   .wrapper-main_img:hover {
    cursor: pointer;
   }

   .wrapper-main_leftBlock {
    display: flex;
    justify-content: center;
    align-items: center;
   }

   .wrapper-main_leftBlock-main {
    height: 50px;
    width: 50px;
   }
  }

  .carousel {
   width: 5vw;
   height: 100%;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;

   .carousel-item {
    position: relative;
    height: 4vw;
    width: 4vw;
    margin-top: 3vh;
    overflow: hidden; 
    border: 1px solid #E8E8E8;
    border-radius: 50%;
   }

   .small-image {
    position: relative;
    margin-left: auto;
    bottom: 0.3vw;
    width: 1vw;
    height: 1vw;
    transform: translate(-50%, -50%);
    visibility: hidden;
   }

   .small-image_visiable {
    visibility: visible;
   }

   .carousel-item:hover, .carousel-control-next:hover, .carousel-control-prev:hover {
    cursor: pointer;
   }

   .carousel-control-next {
    height: auto;
    width: 1vw;
    padding-top: 2vw;
   }

   .carousel-control-prev {
    height: auto;
    width: 1vw;
   }
  }
  .small-image {
   top: 5%;
   left: 5%;
   transform: translate(-50%, -50%);
   z-index: 1;
 }

  .wrapper-main_block {
   width: 60%;
   display: flex;
  }
  .wrapper-main_block > :first-child {
   height: 100%;
   width: 50%;
  }

  .wrapper-main_block > *:not(:first-child) {
   height: 100%;
  }

  .image-container {
   position: inherit;
   max-width: 50%;
   height: auto;
   overflow: hidden;
  }

  .responsive {
   width: 100%;
   height: auto;
  }
 }

 .wrapper-footer {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  .wrapper-footer_container {
   display: flex;
   justify-content: space-between;
   width: 60vw;
  }

  .wrapper-footer_card {
   display: flex;
   width: 32vw;
  }

  .wrapper-footer_description {
   margin-left: 2vw;
   line-height: 0.5;
  }

  .wrapper-footer_description:first-child {
   font-size: 2vw;
   font-weight: bold;
   margin-left: 1vw;
  }

  .wrapper-footer_description > *:not(:first-child) {
   font-size: 1vw;
   color:#666666;
  }

  .footer-logo {
   width: 20%;
   height: auto;
  }
 }
}

.wrapper-main_leftBlock {
 display: flex;
 justify-content: center;
 align-items: center;
 width: 50%;
}

.wrapper-main_leftBlock-main {
 height: 50%;
 width: 100%;
}

.wrapper-main_leftBlock-secondLine {
  font-size: 10vw;
}

.wrapper-main_leftBlock-thirdLine {
  font-size: 1.4vw;
}

.image-with-text {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  color:#FF5C00;
  font-size: 1.2vw;
}

.image-with-text img {
  margin-left: 1vw;
  width: 3vw;
  height: auto;
}

.image-with-text img:hover {
  cursor: pointer;
}

.wrapper-main_button {
  width: 18vw;
  height: 3vw;
  background-color: #FF5C00;
  border-radius: 1.5vw 1.5vw 1.5vw 1.5vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.button-text {
  color: #FFFFFF;
  font-size: 1.3vw;
}

.wrapper-main_button img {
  margin-left: 1vw;
  width: 1.5vw;
  height: auto;
}

